

import { Routes, Route, BrowserRouter, Navigate,Outlet  } from 'react-router-dom';
import Home from './page/Home';
import Oferta from './page/oferta';
import Error from './component/Error';
import Menu from './component/Menu';
import Wnetrza3D from './page/Wnetrza3D';
import Newsroom from './page/Newsroom';
import Panel from './page/panelArchiteke';
import Kontakt from './page/kontakt';


function App() {
  
  return (
    <>
      <BrowserRouter>
   
          <Menu /> 
        <Outlet />
        <Routes>
         
       <Route  exact path="/"  element={<Home />} /> 
        <Route path="oferta" element={<Oferta />} />
        <Route path="Newsroom" element={<Newsroom />} />
         <Route path="panelArchiteke" element={<Panel />} />
         <Route path="kontakt" element={<Kontakt />} />
         
          <Route path="Wnetrza3D" element={<Wnetrza3D />} />
         <Route path='*' element={<Navigate to='/' />} />
          
         <Route path='*' element={<Navigate to='/' />} />
         
          <Route path="*" element={<Error />} /> 
         
        </Routes> 
      
      </BrowserRouter>
    </>
  );

}

export default App;